.toast-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 1000;
}

.toast {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 24px;
  border-radius: 12px;
  color: #ffffff;
  min-width: 320px;
  max-width: 420px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: slideInRight 0.4s cubic-bezier(0.4, 0, 0.2, 1),
    slideOutRight 0.3s ease forwards 2.7s;
}

.toast-icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  background-color: rgba(255, 255, 255, 0.2);
}

.toast-message {
  flex-grow: 1;
  font-size: 14px;
  color: #ffffff;
}

.toast-close {
  flex-shrink: 0;
  background: none;
  border: none;
  color: white;
  opacity: 0.7;
  cursor: pointer;
  font-size: 18px;
  padding: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s ease;
}

.toast-close:hover {
  opacity: 1;
}

.toast.success {
  background-color: #4caf50;
}

.toast.error {
  background-color: #ff4646;
  color: #ffffff;
}

.toast.error .toast-message {
  color: #ffffff;
}

.toast.info {
  background-color: #2196f3;
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideOutRight {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}

/* Page transition animations */
.page {
  animation: fadeInUp 0.3s ease;
}

.page.exit {
  animation: fadeOutDown 0.3s ease forwards;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(20px);
  }
}

/* Card hover animations */
.student-card,
.coach-card {
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.student-card:hover,
.coach-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px rgba(199, 244, 100, 0.15);
}

/* Form animations */
.add-form {
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
